export default {
	data() {
		return {
			pageNum:1,
			teacherInfo:null,
			loadModal:false
			
		}
	},
	methods: {
		loadData()
		{
			var me = this
			uni.request({
				url: this.serverUrl + '/led/manager/teacher/' + this.pageNum + '?name=&limit=1',
				method: 'GET',
				data: {},
				success: res => {
					if(this.teacherInfo!=null && this.teacherInfo.id == res.data[0].id)
					{
						uni.showToast({
							title: "到底了!"
						})
						this.pageNum--
						this.loadModal = false
						return
					}
					
					me.teacherInfo = res.data[0]
					this.loadModal = false
				
				},
				fail: () => {},
				complete: () => {}
			});
		},
		prevent()
		{
			
			if(this.pageNum>1)
			{
				this.loadModal = true
				this.pageNum--
				this.loadData()
				
			}
		},
		next()
		{
			this.loadModal = true
			this.pageNum++
			this.loadData()
		},
		showTeacherLedgerInfo()
		{
			uni.navigateTo({
				url: "../ledger_teacher_detail/ledger_teacher_detail?id=" + this.teacherInfo.id
			})
		}
	},
	onLoad() {
		this.loadData()
	}
}
